@import '../../vars';

.sectionTitle__wrap {
  padding: 130px 10px 0;
  background: linear-gradient(
                  180.6deg, #E7F7FD 47.51%,
                  rgba(231, 247, 253, 0.572544) 67.94%,
                  rgba(231, 247, 253, 0) 111.37%);

  @media(min-width: 768px) {
    padding: 155px 84px 0 82px;
  }
  @media(min-width: 1600px) {
    padding: 240px 0 0 0;
  }
}

.sectionTitle__container {
  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 744px auto;
    grid-template-rows: 1fr;
  }
}

.sectionTitle__title {
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 47px;
  text-align: center;
  margin-bottom: 30px;
  color: $maimTextColor;

  @media(min-width: 768px) {
    font-size: 41px;
    line-height: 53px;
    height: 116px;
    width: 602px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    font-size: 54px;
    line-height: 70px;
    text-align: left;
    width: unset;
    height: unset;
  }
}

.sectionTitle__description {
  font-size: 21px;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
  margin-bottom: 30px;
  color: $subTitlecolor;

  @media(min-width: 768px) {
    height: 116px;
    width: 602px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    width: 658px;
    text-align: left;
    margin: 0 0 30px 0;
  }
}

.sectionTitle__content {
  @media(min-width: 480px) {
    padding: 0 20px;
  }

  @media(min-width: 768px) {
    padding: 0;
  }
}


/*  sectionTitle 8 style */

.sectionTitle__wrap2 {
  padding: 35px 10px 12.5px;
  background: linear-gradient(0, #E7F7FD 100%, rgba(231, 247, 253, 0.572544) 32.94%, rgba(231, 247, 253, 0) 76.37%);

  @media(min-width: 768px) {
    padding: 60px 0 28.5px;
  }

  @media(min-width: 1600px) {
    padding: 150px 10px 42.5px;
  }
}


.sectionTitle__title2 {
  text-transform: uppercase;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: 41px;
  text-align: center;
  margin-bottom: 30px;
  color: $maimTextColor;

  @media(min-width: 768px) {
    font-size: 36px;
    line-height: 49px;
    width: 461px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    font-size: 48px;
    line-height: 65px;
    width: 614px;
    height: 65px;
  }
}

.sectionTitle__container2 {
  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    grid-template-rows: 1fr;
  }
}
