 @import '../../components/vars';

.logo__type {
  width: 44.17px;
  height: 49.84px;

  &:hover {
    cursor: pointer;
  }
  @media(min-width: 768px) {
    width: 66.14px;
    height: 74.76px;
  }
}

.logo__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  width: 189px;
  @media(min-width: 768px) {
    width: 282.65px;
    height: 66.14px;
  }
}

.logo__name__wrap {
  height: 25.63px;
  width: 134.66px;
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;


  @media(min-width: 768px) {
    width: 201.64px;
    height: 38.45px;
    margin-bottom: -5px;
  }

  &:hover {
    cursor: pointer;
  }
}

.logo__mainName {
  font-size: 14px;
  color: $logoColor;
  @media(min-width: 768px) {
    font-size: 24px;
  }
}

.logo__name {
  font-size: 6px;
  font-weight: 300;
  color: $logoColor;

  @media(min-width: 768px) {
    font-size: 10px;
    width: unset;
    height: unset;
  }

}

.logo__link {
  text-decoration: none;
  line-height: 15px;
}

.logo__type__wrap {
  &:hover {
    cursor: pointer;
  }
}


/*  style for footer section */

 .footer__logo__mainName {
   font-size: 26px;
   color: $mainLogoName;

   @media(min-width: 768px) {
     font-size: 32px;
   }
 }

 .footer__logo__name{
   font-size: 11px;
   font-weight: 300;
   color: $logoName;

   @media(min-width: 768px) {
     font-size: 13px;
     margin-top: 11.5px;
   }
 }

 .footer__logo__type {
   width: 70px;
   height: 79px;

   @media(min-width: 768px) {
     width: 84px;
     height: 95px;
   }
 }

 .footer__logo__container {
   width: 300px;
   height: 79px;
   display: flex;
   align-items: center;
   margin-bottom: 60px;

   @media(min-width: 768px) {
     width: 360px;
     height: 95px;
   }
 }

 .footer__logo__name__wrap {
   height: 41.6px;
   width: 217.5px;
   margin: 0 0 0 12.5px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;

   @media(min-width: 768px) {
     width: 261px;
     height: 25.8px;
     margin-bottom: 22px;
   }

   &:hover {
     cursor: pointer;
   }
 }