@import "../../vars";

.contacts__pic {
  width: 300px;
  height: 199px;

  @media(min-width: 768px) {
    width: 598px;
    height: 396px;
  }
  @media(min-width: 1600px) {
    width: 570px;
    height: 377px;
  }
}

.contacts__img {
  height: 199px;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    width: 598px;
    height: 396px;
  }
  @media(min-width: 1600px) {
    width: 570px;
    height: 377px;
    margin: 0 0 110px 0;
  }
}

.contacts__wrapper {
  width: 300px;
  margin: 0 auto;
  padding-top: 130px;

  @media(min-width: 768px) {
    width: 598px;
    padding-top: 165px;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    padding-top: 236px;
    display: flex;

  }
}


.contacts__name {
  font-family: 'Mukta', sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 47px;
  text-align: left;
  color: $titleColor;


  @media(min-width: 768px) {
    font-size: 54px;
    line-height: 70px;
  }

}

.contacts__item {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  text-align: left;
  color: $item;
  margin-top: 30px;

  @media(min-width: 768px) {
    font-size: 21px;
    font-weight: 400;
    line-height: 32px;
    margin-top: 0;
  }
  @media(min-width: 1600px) {
    margin-top: 15px;
  }

}

.contacts__info {
  font-size: 18px;
  font-weight: 700;
  line-height: 27px;
  text-align: left;
  color: $item;

  @media(min-width: 768px) {
    font-size: 21px;
    font-weight: 700;
    line-height: 32px;
  }
}

.contacts__wrap__info {
  @media(min-width: 768px) {
    align-items: end;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 30px;
    width: 569px;
    padding-right: 31px;
    margin-top: 30px;
  }
  @media(min-width: 768px) {
    width: unset;
  }
}

.contacts__info_container {
  margin-bottom: 60px;
  @media(min-width: 1600px) {
    margin: 0 0 0 60px;
  }
}