@import '../../../vars';

.section5__container {
  padding: 0 0 60px;
  width: 300px;
  margin: 0 auto;
  @media(min-width: 768px) {
    width: 600px;
    padding: 0 0 120px;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    padding: 0 0 150px;
  }
}

.section5__title {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 45px;
  text-align: center;
  color: $maimTextColor;
  margin-bottom: 60px;

  @media(min-width: 768px) {
    font-size: 41px;
    line-height: 61.5px;
    margin-bottom: 80px;
  }
  @media(min-width: 1600px) {
    font-size: 48px;
    line-height: 72px;
    margin-bottom: 60px;
    padding: 0 50px 0;
  }
}

.step, .step_description,{
  font-size: 21px;
  font-weight: 400;
  line-height: 31.5px;
  text-align: center;
}

.step {
  color: $maimTextColor;
}

.step_count {
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  text-align: center;
  color: $subTitleColor;
  margin-bottom: 10px;
}

.step_name {
  font-size: 21px;
  font-weight: 700;
  line-height: 31.5px;
  text-align: center;
  color: $subTitlecolor;
  margin-bottom: 10px;
}


.step_description {
  color: $subTitlecolor;
  margin-bottom: 30px;
}

.section5__steps__container {
  @media(min-width: 768px) {
    display: none;
  }
}

.step__pic {
  display: none;
  @media(min-width: 768px) {
    display: block;
    width: 600px;
    margin: 0 auto;
  }
}

.section5__pic {
  width: 600px;
  margin: 0 auto;
}