@import '../../../vars';


.section4__wrapper {
  background-color: $sectionBG;
  margin-bottom: 90px;

  @media(min-width: 768px) {
    padding: 105px 0;
    margin-bottom: 120px;
  }
  @media(min-width: 1600px) {
    padding: 150px 0;
    margin-bottom: 150px;
  }
}

.section4__container {
  @media(min-width: 1600px) {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
  }
}

.section4__info__wrap {
  display: grid;
  justify-items: flex-end;
  padding: 30px 10px 60px 35px;

  @media(min-width: 768px) {
    padding: unset;
    width: 600px;
    margin: 0 auto;
  }
  @media(min-width: 1600px) {
    width: 586px;
  }

}

.section4__list {
  color: $listColor;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    margin-bottom: 15px;
  }
}

.section4__info__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 27px;
  text-align: left;
  margin-bottom: 15px;
  color: $maimTextColor;

  @media(min-width: 768px) {
    margin-bottom: unset;
  }
}

.section4__info__item {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  text-align: left;
  color: $maimTextColor;
}

.section4__info__container {
  margin: 0 auto;
  width: 275px;

  @media(min-width: 768px) {
    width: 570px;
    margin: 0;
    //width: 554px;
  }
}

.section4__title {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 39px;
  color: $maimTextColor;
  text-align: center;

  @media(min-width: 768px) {
    font-size: 48px;
    line-height: 62px;
  }
}

.section4__subTitle {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 39px;
  text-align: center;
  color: $subTitleColor;

  @media(min-width: 768px) {
    font-size: 70px;
    font-weight: 700;
    line-height: 91px;

  }
  @media(min-width: 1600px) {
    font-size: 68px;
    line-height: 104px;
  }
}

.section4__title__wrap {
  padding: 90px 10px 0;
  width: 300px;
  margin: 0 auto;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto 60px;
    padding: unset;
  }
  @media(min-width: 1600px) {
    width: 569px;
    margin: 0 60px 0 0;
  }
}

