@import "../../vars";

.dropMenu__nav__clicked {
  width: 150px;
  //width: 140px;
  height: 102px;
  list-style-type: none;
  background: $dropBg;
  border-radius: 10px;
  text-decoration: none;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 20px 25px;
  justify-content: space-between;
  box-shadow: inset 0 0 0 1000px #0000000a;
}

.drop_element {
  text-decoration: none;
  color: $maimTextColor;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;

  &:hover {
    color: $dropMenuHover;
  }
}

.drop_wrap {
  position: absolute;
  //right: 218px;
  //right: 92px;
  right: 104px;
  height: 145px;
  width: 145px;
  padding: 20px 0;
}

.dropMenu__nav {
  display: none;
}