@import "../../../vars";

.section3__wrap {
  padding: 0 10px;
}

.section3__title {
  text-transform: uppercase;
  font-size: 28px;
  font-weight: 700;
  line-height: 36px;
  text-align: center;
  color: $maimTextColor;
  margin-bottom: 60px;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto 90px;
    font-size: 41px;
    line-height: 56px;
  }
  @media(min-width: 1600px) {
    width: 1020px;
    margin: 0 auto 92px;
    font-size: 48px;
    line-height: 65.37px;
  }
}

.section3_items_wrap {
  width: 300px;
  margin: 0 auto;
  display: grid;
  @media(min-width: 768px) {
    width: 600px;
    justify-content: center;
    margin-bottom: 60px;
  }

  @media(min-width: 1600px) {
    width: 1200px;
    grid-template-columns: 360px 360px 360px;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 140px;
  }
}

.section3_items {
  display: grid;
  justify-items: center;
  margin-bottom: 60px;

  @media(min-width: 768px) {
    width: 360px;
  }
  @media(min-width: 1600px) {
    margin-bottom: unset;
  }

}

.item_description {
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}

.item_pic {
  width: 122px;
  height: 90px;
  margin-bottom: 30px;
}
