@import '../../vars';

.sectionStatistic__4_wrap {
  margin: 90px auto 0;
  background-color: $bgSectionStatistic;
}

.sectionStatistic__4_container {
  padding: 90px 10px;
  width: 320px;
  margin: 0 auto;

  @media(min-width: 768px) {
    width: 599px;
    padding: 120px 0;
  }
  @media(min-width: 1600px) {
    padding: 150px 0;
    width: 1200px;
    display: flex;
    justify-content: space-between;
  }
}

.statistic__title {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 42px;
  text-align: left;
  color: $maimTextColor;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    width: 599px;
    height: 100px;
    font-size: 36px;
    font-weight: 700;
    line-height: 50px;
    text-align: left;
    margin-bottom: 15px;
  }

  @media(min-width: 1600px) {
    width: 569px;
    font-size: 36px;
    line-height: 50px;
    text-align: left;
  }
}

.statistic__description {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  text-align: left;
  margin-bottom: 60px;

  @media(min-width: 768px) {
    height: 54px;
    width: 599px;
    margin-bottom: 0;
  }

  @media(min-width: 1600px) {
    width: 569px;
  }
}


.statistic__topics_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media(min-width: 768px) {
    align-items: flex-start;
  }
}

.statistic {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    width: 570px;
    height: 65px;
    flex-direction: row;
    margin: 30px 0 0 0;
  }

  @media(min-width: 1600px) {
    margin: unset;
  }
}

.statistic__topic {
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
  text-align: center;
  color: $maimTextColor;

  @media(min-width: 768px) {
    margin-left: 10px;
  }
  @media(min-width: 1600px) {
    font-size: 24px;
    font-weight: 400;
    line-height: 33px;
    text-align: left;
  }
}

.statistic__numbers {
  font-size: 48px;
  font-weight: 400;
  line-height: 65px;
  text-align: center;
  color: $numbersColor;
}