@import "../../vars";

.sectionTitle_3__wrap {
  padding: 60px 15px 0;
  width: 301px;
  height: 576px;
  margin: 96px auto 90px;
  background: url("../../../assets/bg/blue3.png"), linear-gradient(268.11deg, #31A7EA 1.6%, #1082C2 98.4%) no-repeat;
  background-size: cover;
  box-shadow: 0 4px 8px rgba(16, 101, 152, 0.16);
  border-radius: 30px;

  @media(min-width: 768px) {
    background: url("../../../assets/bg/blue2.png"), linear-gradient(268.11deg, #31A7EA 1.6%, #1082C2 98.4%) no-repeat;
    background-size: cover;
    padding: 90px 60px 0;
    width: 600px;
    height: 511px;
    margin: 120px auto 120px;
  }
  @media(min-width: 1600px) {
    background: url("../../../assets/bg/blue1.svg"), linear-gradient(268.11deg, #31A7EA 1.6%, #1082C2 98.4%) no-repeat;
    background-size: cover;
    width: 1200px;
    height: 319px;
    margin: 150px auto 150px;
    padding: 90px;
  }
}

.sectionTitle_3__title {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 41px;
  text-align: left;
  margin-bottom: 30px;
  color: $TextWhite;

  @media(min-width: 768px) {
    font-size: 36px;
    line-height: 49px;
    width: 480px;
    height: 98px;
  }

  @media(min-width: 1600px) {
    margin-bottom: 15px;
    font-size: 36px;
    font-weight: 700;
    line-height: 49px;
    text-align: left;
    width: 756px;
    height: 49px;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.sectionTitle_3__description {
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
  text-align: left;
  margin-bottom: 30px;
  color: $TextWhite;

  @media(min-width: 1600px) {
    width: 768px;
    height: 75px;
    text-align: left;
    margin: unset;
  }
}

.sectionTitle_3__content {
  @media(min-width: 1600px) {
    display: grid;
    grid-template-columns: 768px auto;
  }
}


