@import "../../vars";

.card_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  border-radius: 30px;
  box-shadow: 0 4px 8px rgba(16, 101, 152, 0.16);
  margin: 0 auto 60px;
  //margin: 0 auto 55px;
  background-color: $cardBg;
  padding: 60px 15px;

  @media(min-width: 768px) {
    width: 360px;
    //margin: 0 auto 60px;
  }

  @media(min-width: 1600px) {
    margin: unset;
  }

}

.card__pic {
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
}

.card__title {
  font-size: 21px;
  font-weight: 700;
  line-height: 29px;
  text-align: center;
  margin-bottom: 15px;
  color: $subTitlecolor;

  @media(min-width: 768px) {
    width: 300px;
    margin: 0 auto 15px;
  }
}

.card__description {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  color: $subTitlecolor;

  @media(min-width: 768px) {
    width: 300px;
    margin: 0 auto 15px;
  }
}

.section__AboutUs_2__container {
  @media(min-width: 1600px) {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}

.section__AboutUs_2__wrap {
//.section__AboutUs_2__wrap2 {
//  background: linear-gradient(0deg, #E7F7FD 0.51%, rgba(231, 247, 253, 0.572544) 32.94%, rgba(231, 247, 253, 0) 76.37%);
  display: flex;
  flex-direction: column;
  align-self: center;

  ///
  padding-bottom: 30px;

  @media(min-width: 768px) {
    padding-bottom: 90px;
  }
}


/* section OurService styling */

.card__pic.section__OurServices {
  width: 102px;
  height: 108px;
  margin: 0 0 30px;

  @media(min-width: 768px) {
    width: 120px;
    height: 120px;
    margin: unset;
  }
}

.card__title.section__OurServices {
  font-size: 24px;
  font-weight: 700;
  line-height: 33px;
  text-align: center;
  color: $maimTextColor;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    text-align: left;
  }
}

.card__description.section__OurServices {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: left;
  color: $subTitlecolor;
}

.card_wrap.section__OurServices {
  @media(min-width: 768px) {
    width: 570px;
    height: 348px;
    display: grid;
    grid-template-columns: 300px auto;
    padding: 60px;
  }
}

.card_img.section__OurServices {
  @media(min-width: 768px) {
    order: 2;
    align-self: center;
    margin-left: 30px;
  }
}
