@import '../vars';

.btn__wrap {
  width: 138px;
  height: 48px;
  background: linear-gradient(268.11deg, #1082C2 1.6%, #31A7EA 98.4%, #31A7EA 98.4%);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 77.5px;
  border: 1px solid transparent;

  @media(min-width: 1600px) {
    margin: 0 0 107.5px 0;
  }

  &:hover {
    background: $bghoverBtn1;
    border: 1px solid $hoverBtn1;
    cursor: pointer;
    color: $hoverBtn1;

    & > .sectionTitle__btn {
      color: $hoverBtn1;
    }
  }

}
button:focus, button:active {
    outline: none;
}

//.btn_clicked {
//  transition: 0.5s;
//  background: linear-gradient(to right, #fbc2eb 0%, #a6c1ee 51%, #fbc2eb 100%);
//}

.sectionTitle__btn {
  color: $btnText1;
  text-decoration: none;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;

}



/*   styling for sectionTitle_3__btn   */

.btn__wrap2 {
  width: 222px;
  height: 48px;
  background: linear-gradient(90deg, #F8CE25 0%, #F5A915 100%);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 60px;
  border: 1px solid transparent;

  @media(min-width: 768px) {
    margin: 0 0 90px 0;
    justify-self: flex-start;
  }

  @media(min-width: 1600px) {
    margin: 0 0 0 30px;
  }

  &:hover {
    background: $bghoverBtn1;
    border: 1px solid $hoverBtn2;
    cursor: pointer;
  }

}

.sectionTitle_3__btn {
  font-size: 14px;
  font-weight: 700;
  line-height: 19px;
  text-align: center;
  color: $maimTextColor;
  text-decoration: none;
}

/*   styling for sectionAboutUs_7__btn   */

.arrow__btn__wrap {
  width: 120px;
  height: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
}
.sectionService__btn {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  text-align: left;
  color: $maimTextColor;
  vertical-align: center;

  &:hover {
    color: $iconBtnHover;
  }
}

