@import '../../../vars';

.section2__container {
  padding: 135px 10px 90px;
  @media(min-width: 768px){
    padding: 135px 10px 30px;
  }
  @media(min-width: 1600px){
    width: 1200px;
    padding: 165px 0 60px;
    margin: 0 auto;
  }
}

.section2__title {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  line-height: 41px;
  text-align: center;
  color: $maimTextColor;
  margin-bottom: 60px;

  @media(min-width: 768px){
    width: 600px;
    height: 98px;
    margin: 0 auto 90px;
    font-size: 36px;
    line-height: 49px;
  }
  @media(min-width: 1600px){
    width: 1026px;
    height: 65px;
    margin: 0 auto 90px;
    font-size: 48px;
    line-height: 65.37px;
  }
}
.section2__pic {
  width: 300px;
  height: 324px;

  @media(min-width: 768px){
    width: 278px;
    height: 300px;
  }
}

.topic1__wrap {
  width: 300px;
  height: 324px;
  margin: 0 auto 30px;

  @media(min-width: 768px){
    width: 278px;
    height: 300px;
  }
  @media(min-width: 1600px){
    margin: 0 auto 0;
  }
}

.topic2__wrap {
  width: 300px;
  height: 324px;
  margin: 0 auto 30px;

  @media(min-width: 768px){
    width: 278px;
    height: 300px;
  }
  @media(min-width: 1600px){
   order: 2;
  }
}
.topic__item__container{
  margin-bottom: 30px;
  @media(min-width: 768px) {
    width: 597px;
    height: 128px;
    margin: 0 auto 90px;
  }
  @media(min-width: 1600px) {
    width: unset;
    height: unset;
    margin: unset;
  }
}

.topic__item {
  font-size: 21px;
  font-weight: 400;
  line-height: 31.5px;
  text-align: center;
  color: $maimTextColor;

  @media(min-width: 1600px) {
    text-align: left;
  }
}

.section2__topic1 {
  @media(min-width: 1600px) {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    grid-column-gap: 30px;
    width: 878px;
    margin: 0 auto 90px;
  }
}


