@import "../../vars";

/* Position and sizing of burger button */

.bm-burger-button {
  position: fixed;
  width: 24px;
  height: 18px;
  right: 15px;
  top: 33px;

  @media(min-width: 768px) {
    top: 39px;
    //right: 95px;
    position: absolute;
    right: 0;
  }

  &:focus {
    outline: unset;
  }
  @media(min-width: 1600px) {
    display: none;
  }

}

#react-burger-menu-btn:focus {
  outline: unset;
}

#outer-container {
  width: 80%;
  @media(min-width: 1600px) {
    display: none;
  }
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: $maimTextColor;
}

/* shape our bars  */
.burger__bars {
  //height: 2px !important;
  height: 1.5px !important;
  background: midnightblue;
  // only words color
}


/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: $maimTextColor;
}

//if need crossbar
/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 18px;
  width: 24px;
  top: 21px;

}

/* Color/shape of close button cross */
.bm-cross {
  background: $maimTextColor;
  width: 2px !important;
  height: 20px !important;
}

/* This positions the cross centrally in the button */
.bm-cross-button > span {
  top: 23px !important;
}

/*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  @media(min-width: 768px) {
    width: 477px !important;

    //width: 362px !important;
  }
}

/* General sidebar styles */
.bm-menu {
  background: white;
  font-size: 16px;
  line-height: 24px;
  background: linear-gradient(180.6deg, #E7F7FD 100%, rgba(231, 247, 253, 0.572544) 32.94%, rgba(231, 247, 253, 0) 76.37%);

}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  padding: 20px 10px 0;

  @media(min-width: 768px) {
    padding: 20px 40px 0;
  }
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: #212529;
  margin-bottom: 20px;
  margin-top: 20px;
  text-align: left;
  text-decoration: none;
  transition: color 0.2s;
  outline: none;
  padding-left: 10px;
}


.bm-item:hover {
  color: $maimTextColor;
}

.bm-item:active {
  color: $maimTextColor;
}


