@import "../../vars";

.navBar__header {
  display: none;

  @media(min-width: 1600px) {
    display: flex;
  }
}

.navBar__header__li {
  list-style-type: none;
  border-radius: 10px;
  height: 42px;
  display: flex;
  align-items: center;

  &:hover {
    background: rgba(82, 116, 151, 0.1);
    color: $dropMenuHover;
    transition: 0.2s all ease-in-out;

    & > a {
      & > .fas {
        transform: rotate(180deg);
      }
    }
  }
}

.navBar__item {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  color: $maimTextColor;
  text-decoration: none;
  cursor: pointer;
  margin: 0 20px;
}

.navBar__nav {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
//////////////////
  position: relative;
}

.fas.fa-chevron-down {
  margin-left: 10px;
  font-size: 10px;
  color: $maimTextColor;
}


