@import '../../components/vars';

.section404__wrap {
  padding: 130px 0 12.5px;
  width: 300px;
  margin: 0 auto;

  @media(min-width: 768px){
    padding: 155px 0 42.5px;
    width: 600px;
  }

  @media(min-width: 1600px){
    width: 1200px;
    padding: 215px 0 120px;
  }
}

.section404__pic__wrap, .section404__pic {
  height: 269px;
  margin: 0 auto 30px;

  @media(min-width: 768px){
    height: 538px;
    width: 600px;
  }
  @media(min-width: 1600px){
    width: 570px;
    height: 510px;
    margin: unset;
  }
}

.subtitle__404, .section404__title {
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 43px;
  text-align: center;

  @media(min-width: 768px){
    font-size: 54px;
    line-height: 64.8px;
    text-align: left;
  }
  @media(min-width: 1600px){
    font-size: 54px;
    line-height: 89.75px;
  }
}

.section404__pic__wrap {
  @media(min-width: 1600px){
   order: 2;
  }
}

.section404__title {
  margin: 0 auto 30px;
}

.section404__description {
  font-family: 'Mukta', sans-serif;
  font-size: 21px;
  font-weight: 400;
  line-height: 32px;
  text-align: left;
  color: $subTitlecolor;
  margin-bottom: 30px;
  @media(min-width: 1600px){
    line-height: 34px;
  }
}

.section404__content {
  @media(min-width: 768px){
    width: 570px;
    margin: 0 auto;
  }
}

.section404__container {
  @media(min-width: 1600px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 60px;
  }
}