@import '../vars';

.footer__wrap {
  background-color: $footerBg;
  padding: 90px 0;
}

.footer__container {
  width: 300px;
  margin: 0 auto;

  @media(min-width: 768px) {
    width: 360px;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    display: grid;
    grid-template-columns: 360fr 370fr 229fr;
    grid-column-gap: 121px;
  }

}

.footer__nav {
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-align: left;
  color: $footerNavTitle;
  margin-bottom: 60px;

  @media(min-width: 768px) {
    margin-bottom: 45px;
    display: flex;
    width: 370px;
    justify-content: space-between;
  }
}

.footer__contacts {
  margin-bottom: 45px;

  @media(min-width: 1600px) {
    height: 111px;
    border-right: 1px;
    border-color: white;
    border-right-style: solid;
    padding: 0 16px 0 0;
  }
}

.contacts__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 33px;
  text-align: center;
  color: $footerNavTitle;
  padding-bottom: 15px;

  @media(min-width: 1600px) {
    text-align: end;
  }
}


.contacts__email, .contacts__phone {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  padding-bottom: 15px;
  color: $links;

  @media(min-width: 1600px) {
    text-align: end;
  }
}

.footer__rights {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;

  @media(min-width: 1600px) {
    display: flex;
    align-items: flex-end;
  }
}

.contacts__copy {
  color: $footerNavTitle;
  display: flex;
  justify-content: center;
}

.contacts__rights {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  color: $links;

  @media(min-width: 1600px) {
    margin-left: 5px;
  }
}

.footer__copy {
  margin-left: 5px;
}