.header__wrap {
  background-color: rgb(231, 247, 253);
  height: 80px;
  position: fixed;
  width: 100%;
  z-index: 1000;

  @media(min-width: 768px) {
    height: 100px;
  }
  @media(min-width: 1600px) {
    height: 95px;
  }
}

.header__wrap.active {
  background-color: rgb(231, 247, 253);
}

.header {
  padding: 20px 10px 0 10px;
  margin: 0 auto;
  z-index: 3;
  position: relative;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto;
  }
  @media(min-width: 1600px) {
    margin: 0 auto;
    height: 75px;
    grid-template-columns: auto 1fr auto;
    width: 1200px;
    display: grid;
  }
}