@import '../../components/vars';

/* section 1 Title  */
.sectionWL__wrap {
  padding: 124px 10px 45px;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto;
    padding: 155px 0 0;
  }
  @media(min-width: 1600px) {
    padding: 215px 0 0 0;
    width: 1200px;
  }
}

.sectionWL__container {
  @media(min-width: 1600px) {
    display: grid;
    grid-template-columns: 630px 570px;
  }
}

.sectionWL__pic__wrap {
  width: 300px;
  margin: 0 auto 79px;

  @media(min-width: 768px) {
    width: 600px;
    height: 482px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    width: 570px;
    height: 457.59px;
    order: 2;
    margin: -35px 0 0 20px;
  }
}

.sectionWL__pic {
  width: 300px;
  height: 242px;

  @media(min-width: 768px) {
    width: 600px;
    height: 482px;
  }
  @media(min-width: 1600px) {
    width: 570px;
    height: 457.59px;
  }
}

.subtitle__WL {
  display: block;
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 46.8px;
  text-align: left;
  margin-right: 60px;
  color: $maimTextColor;

  @media(min-width: 768px) {
    margin-right: unset;
    font-size: 31.5px;
    line-height: 53px;
  }
  @media(min-width: 1600px) {
    font-size: 54px;
    line-height: 70.2px;
    width: 300px;
  }
}


.sectionWL__title {
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 46.8px;
  text-align: left;
  color: $subTitleWL;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    font-size: 31.5px;
    line-height: 48px;
  }
  @media(min-width: 1600px) {
    font-size: 64px;
    line-height: 64px;
  }
}

.sectionWL__description {
  font-size: 21px;
  font-weight: 400;
  line-height: 32px;
  text-align: left;
  margin-bottom: 30px;
  color: $subTitlecolor;
  @media(min-width: 768px) {
    padding-right: 20px;
  }
  @media(min-width: 1600px) {
    padding-right: 188px;
  }
}

.sectionWL__content {
  width: 300px;
  margin: 0 auto;

  @media(min-width: 768px) {
    width: 600px;
  }
  @media(min-width: 1600px) {
    width: unset;
  }
}

.section2__container.sectionWL {
  padding: 0;
  width: 300px;
  margin: 0 auto;

  @media(min-width: 768px) {
    width: 600px;
    padding-bottom: 40px;
  }
  @media(min-width: 1600px) {
    width: 1035px;
    padding: unset;
    margin: 0 auto;
  }
}

.topic1__wrap.sectionWL {
  width: 301px;
  height: 264px;
  margin: 0 auto 30px;

  @media(min-width: 768px) {
    width: 399px;
    height: 350px;
  }
  @media(min-width: 1600px) {
    margin: 0 auto 0;
  }
}

.section1__pic.sectionWL {
  width: 301px;
  height: 264px;

  @media(min-width: 768px) {
    width: 399px;
    height: 350px;
  }
}

.topic2__wrap.sectionWL {
  width: 301px;
  height: 244px;
  margin: 0 auto 30px;

  @media(min-width: 768px) {
    width: 432px;
    height: 350px;
  }
  @media(min-width: 1600px) {
    margin: 0 auto;
  }
}

.section2__pic.sectionWL {
  width: 301px;
  height: 244px;

  @media(min-width: 768px) {
    width: 432px;
    height: 350px;
  }
}

.topic__item.sectionWL {
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
  color: $maimTextColor;

  @media(min-width: 1600px) {
    text-align: left;
  }
}

.topic__item__container.sectionWL {
  margin-bottom: 60px;
  @media(min-width: 768px) {
    width: 597px;
    height: unset;
    margin: 0 auto 60px;
  }
  @media(min-width: 1600px) {
    width: unset;
    margin: unset;
  }
}

.section2__topic1.sectionWL {
  @media(min-width: 1600px) {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    grid-column-gap: 60px;
    width: 1035px;
    margin: 0 auto 60px;
  }
}

.btnWL__wrap {
  width: 138px;
  height: 48px;
  background: linear-gradient(268.11deg, #1082C2 1.6%, #31A7EA 98.4%, #31A7EA 98.4%);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 77.5px;
  border: 1px solid transparent;

  @media(min-width: 768px) {
    margin: 0 0 77.5px 0;
  }

  @media(min-width: 1600px) {
    margin: 0 0 88.5px 0;
  }

  &:hover {
    background: $bghoverBtn1;
    border: 1px solid $hoverBtn1;
    cursor: pointer;
    color: $hoverBtn1;

    & > .sectionTitle__btn {
      color: $hoverBtn1;
    }
  }
}

.title__first {
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 28px;
  display: block;
  font-weight: 700;
  line-height: 46.8px;
  text-align: left;
  color: $maimTextColor;
  margin-right: 10px;

  @media(min-width: 768px) {
    display: unset;
    font-size: 31.5px;
    line-height: 53px;
  }
  @media(min-width: 1600px) {
    font-size: 54px;
    line-height: 70.2px;
  }
}


/* section 2 Intro */


.intro__wrap_2 {
  padding: 30px 10px 60px;
  @media(min-width: 768px) {
    padding: 20px 10px 65px;;
  }
  @media(min-width: 1600px) {
    padding: 90px 10px 60px;
  }
}


.intro.WL {
  font-size: 30px;
  font-weight: 700;
  line-height: 40.85px;
  color: $maimTextColor;
  text-transform: uppercase;
  text-align: center;
  display: block;
  width: 128px;
  margin: 0 15px 0;

  @media(min-width: 768px) {
    width: 323px;
    margin: 0 30px 0;
    font-size: 41px;
    line-height: 55.83px;
  }
  @media(min-width: 1600px) {
    width: 378px;
    font-size: 48px;
    line-height: 65.37px;
  }
}


/* section 3 cards styling  */


.section__AboutUs_2__container2 > .card_wrap.section3_wl:first-child > .card_img.section3_wl:first-child {
  @media(min-width: 768px) {
    order: 2;
    margin-left: -30px;
  }
  @media(min-width: 1600px) {
    width: 278px;
    margin-left: 52px;
  }
}

.aboutUs__content > .card__description.section3_wl {
  @media(min-width: 768px) {
    padding-right: 30px;
  }
}

.section__AboutUs_2__container2 .aboutUs__content {
  @media(min-width: 1600px) {
    width: 390px;
  }
}

.section__AboutUs_2__container2 {
  @media(min-width: 1600px) {
    width: 1200px;
    margin: 0 auto;
    display: unset;
  }
}

.card_wrap.section3_wl {
  padding: 30px 15px;
  align-items: flex-start;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto 30px;
    padding: 60px 30px;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 15px;

    @media(min-width: 1600px) {
      justify-content: space-between;
    }
  }

  @media(min-width: 1600px) {
    width: 840px;
    padding: 60px 60px;
    margin: 0 auto 60px;
    grid-column-gap: unset;
  }
}

.card_img.section3_wl {
  width: 255px;
  height: 140px;
  background: #33507B;
  border-radius: 5px;
  margin-bottom: 16px;

  @media(min-width: 768px) {
    margin-bottom: unset;
  }
  @media(min-width: 1600px) {
    width: 278px;
    height: 150px;
  }
}

.card__pic.section3_wl {
  width: 255px;
  height: 140px;

  @media(min-width: 1600px) {
    width: 278px;
    height: 150px;
  }
}

.card__title.section3_wl {
  font-size: 24px;
  font-weight: 700;
  line-height: 32.68px;
  text-align: left;
  color: $maimTextColor;

  @media(min-width: 1600px) {
    margin: 0 0 30px 0;
    width: unset;
  }
}

.card__description.section3_wl {
  font-size: 16px;
  font-weight: 400;
  line-height: 21.79px;
  text-align: left;
  @media(min-width: 1600px) {
    margin: unset;
    width: unset;
  }
}


/*  Section4 list styling  */

.section4__list.WL {
  @media(min-width: 768px) {
    margin-bottom: 30px;
  }
}

.section4__wrapper.WL {
  @media(min-width: 768px) {
    padding: 120px 0 90px;
  }
  @media(min-width: 1600px) {
    padding: 150px 0;
  }
}
.section4__subTitle.WL {
  @media(min-width: 1600px) {
    font-size: 80px;
    line-height: 104px;
    text-align: center;
  }
}

.bg__for__form {
  @media(min-width: 768px) {
    background: $appBgReverse;
  }
}

.bg__from__start {
  background: $appBg;
}
