@import '../../vars';

.separator {
  display: block;
  width: 50px;
  height: 10px;
  background-color: $bgSeparator;
  border-radius: 2px;

  @media(min-width: 768px) {
    width: 100px;
  }
}

.intro__wrap {
  padding: 90px 10px 60px;

  //@media(min-width: 480px) {
  //  padding: 90px 30px 60px;
  //}
  @media(min-width: 768px) {
    padding: 135px 30px 60px;
  }
}

.intro__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 0;
}

.intro {
  font-size: 29px;
  font-weight: 700;
  line-height: 41px;
  color: $maimTextColor;
  margin: 0 15px;
  text-transform: uppercase;
  text-align: center;

  /* main page */

  &.bigger {
    display: block;
    width: 146px;
    margin: 0 15px 0;

    @media(min-width: 768px) {
      width: 306px;
      margin: 0 30px 0;
    }
    @media(min-width: 1600px) {
      width: 408px;
    }
  }

  @media(min-width: 768px) {
    font-size: 36px;
    line-height: 49px;
    margin: 0 30px 0;

  }
  @media(min-width: 1600px) {
    font-size: 48px;
    line-height: 65.37px;
  }
}

.intro__description {
  font-size: 21px;
  font-weight: 400;
  line-height: 32px;
  text-align: center;
  color: $subTitlecolor;

  @media(min-width: 768px) {
    width: 600px;
    margin: 0 auto;
  }
  @media(min-width: 1600px) {
    width: 840px;
    margin: 0 auto;
  }
}