@import "../../components/vars";

.sectionTitle__pic__wrap {
  @media(min-width: 1600px) {
    order: 2;
  }
}

.sectionTitle__pic {
  height: 205px;
  width: 300px;
  display: block;
  margin: 0 auto 30px;

  @media(min-width: 768px) {
    height: 335px;
    width: 493px;
  }

  @media(min-width: 1600px) {
    margin: 0 auto 0 -40px;
  }

}
