@import "../../components/vars";

.sectionLH__wrap {
  padding: 119px 10px 0;

  @media(min-width: 768px) {
    padding: 215px 0 0 0;
  }
  @media(min-width: 1600px) {
    padding: 220px 0 0 0;
  }
}

.subtitle__LH {
  color: $subTitleLH;
  font-family: 'Mukta', sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  text-align: center;
  text-transform: uppercase;

  @media(min-width: 768px) {
    font-size: 54px;
    line-height: 81px;
    text-align: center;
  }
  @media(min-width: 1600px) {
    width: 1200px;
    font-size: 54px;
    line-height: 70.2px;
    text-align: center;
    margin: 0 auto;
  }
}

.sectionLH__title {
  text-transform: uppercase;
  font-family: 'Mukta', sans-serif;
  font-size: 26px;
  font-weight: 700;
  line-height: 39px;
  text-align: center;
  margin-bottom: 30px;
  color: $maimTextColor;

  @media(min-width: 768px) {
    font-size: 41px;
    line-height: 61.5px;
    width: 600px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    width: 1200px;
    font-size: 48px;
    line-height: 62.4px;
    text-align: center;
  }
}

.sectionLH__description {
  font-size: 21px;
  font-weight: 400;
  line-height: 32px;
  text-align: center;
  margin-bottom: 30px;
  color: $subTitlecolor;

  @media(min-width: 768px) {
    height: 96px;
    width: 599px;
    margin: 0 auto 30px;
  }

  @media(min-width: 1600px) {
    width: 840px;
    height: 64px;
    text-align: center;
    margin: 0 auto 30px;
  }
}

.btnLH__wrap {
  width: 138px;
  height: 48px;
  background: linear-gradient(268.11deg, #1082C2 1.6%, #31A7EA 98.4%, #31A7EA 98.4%);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 77.5px;
  border: 1px solid transparent;

  &:hover {
    background: $bghoverBtn1;
    border: 1px solid $hoverBtn1;
    cursor: pointer;
    color: $hoverBtn1;

    & > .sectionTitle__btn {
      color: $hoverBtn1;
    }
  }
}

.bg__for__form {
  @media(min-width: 768px) {
    background: $appBgReverse;
  }
}

.bg__from__start {
  background: $appBg;
}

.section4__title.sectionLH, .section4__subTitle.sectionLH {
  text-align: left;
}


