@import '../../vars';

.fas.fa-chevron-down.footer, .fas.fa-chevron-up {
  color: $footerNavTitle;
  font-size: 8px;
  margin-left: 0;
}

.nav__title__wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  width: 101px;
  height: 25px;
  text-decoration: none;
}

.nav__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-align: left;
  margin-right: 10px;
  color: $footerNavTitle;

  &_2 {
    display: flex;
    //align-items: center;
    margin-bottom: 15px;
    //width: 101px;
    //height: 25px;
    //text-decoration: none;
  }
}


.footer__nav__item {
  display: block;
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: $links;
  margin-bottom: 15px;

  &:hover {
    color: $footerHoverText;
  }
}


